z-index: -1;\n\tinset: 0;\n\tborder-radius: inherit;\n\tborder: var(--border-width) solid transparent;\n\tbackground: linear-gradient(\n\t\t\trgb(var(--surface-container) / 0.25),\n\t\t\trgb(var(--surface-container) / 0.5)\n\t\t)\n\t\tborder-box;\n\tmask:\n\t\tlinear-gradient(black, black) border-box,\n\t\tlinear-gradient(black, black) padding-box;\n\tmask-composite: subtract;\n}\n\n.weather-data-card {\n\tpadding: 1rem;\n\t/* background-color: rgb(var(--surface-container));\n\tbox-shadow: var(--elevation-1); */\n\tborder-radius: 1rem;\n}\n\n.weather-data-card > .date-selection {\n\tdisplay: flex;\n\tjustify-content: space-between;\n}\n\n.arrow-button {\n\tbackground-color: rgb(var(--primary));\n\tcolor: rgb(var(--on-primary));\n\tborder-radius: 0.25rem;\n}\n\n.arrow-button:hover {\n\tbackground-color: hsl(from rgb(var(--primary)) h s 30%);\n\tscale: 1.05;\n}\n\n.weather-data-card > .heading-subtitle {\n\ttext-align: center;\n}\n\n.heading-subtitle {\n\tmargin: 1.5rem 0;\n}\n\n.heading-subtitle > h1 {\n\tcolor: rgb(var(--primary));\n}\n\n.heading-subtitle > p {\n\tcolor: rgb(var(--secondary));\n\tfont-weight: bold;\n\tfont-size: 0.75rem;\n}\n\n.weather-data-card > .data {\n\tdisplay: grid;\n\tgrid-template-columns: repeat(3, 1fr);\n\tgap: 0.5rem;\n}\n\n.data .material-symbols-outlined {\n\tcolor: rgb(var(--primary-container));\n}\n\n.data > .badge,\n.data > .card {\n\tbackground-color: rgb(var(--primary) / 0.6);\n\tborder: 0.25rem solid rgb(var(--primary));\n\tcolor: rgb(var(--on-primary));\n\tdisplay: flex;\n\tjustify-self: center;\n\tflex-direction: column;\n\talign-items: center;\n\tjustify-content: center;\n\tgap: 0.25rem;\n}\n\n.badge {\n\tborder-radius: 50%;\n\twidth: 94px;\n\theight: 94px;\n}\n\n.data > .card {\n\tdisplay: grid;\n\tgrid-template-columns: repeat(3, 1fr);\n\tgap: 0;\n\tpadding: 0.25rem;\n}\n\n.card {\n\tborder-radius: 0.5rem;\n\tgrid-column: span 3;\n\twidth: 100%;\n}\n\n.card > h2 {\n\tgrid-column: span 3;\n\tmargin: 0.25rem 0;\n\ttext-align: 